import React, { useEffect, useState } from 'react';
import styles from './settings.module.css';
import {useSelector, useDispatch} from 'react-redux';
import { setSettings } from '../../../../general_redux/general settings/actions';
import {Button,Form,InputGroup,Row,Col} from 'react-bootstrap';

function Settings() {
  const {setting, currencyArray, timeFormatArray} = useSelector(({generalSetting}) => generalSetting && generalSetting);
  const [defaultCurrency, setDefaultCurrency] = useState(setting.defaultCurrency);
  const [timeFormat, setTimeFormat] = useState(setting.timeFormat);
  const [dateFormat, setDateFormat] = useState(setting.dateFormat);
  const [numberFormat, setNumberFormat] = useState(setting.numberFormat);
  const [settings, setSetting] = useState(null);
  const dispatch = useDispatch();

  // Load settings on load

  // useEffect((data) => {
  //   setDefaultCurrency(data.defaultCurrency);
  //   setTimeFormat(data.timeFormat);
  //   setDateFormat(data.dateFormat);
  //   setNumberFormat(data.numberFormat);
  // }, []);

  // Handle Save

  function handleSave() {
    setSetting({
      defaultCurrency,
      timeFormat,
      dateFormat,
      numberFormat,
    });
  }

  useEffect(() => {
    if (settings) {
     dispatch(setSettings(settings))
    }
  }, [dispatch, settings])
  return (
    <div className={'p-3'}>
      <h2>General Settings</h2>
      <div className={styles.settings_wrapper}>
        <div className={`input-group mb-3 ${styles.input_group}`}>
          <div className="input-group-prepend">
            <label
              className={`input-group-text ${styles.input_group_text}`}
              htmlFor="propertyType"
            >
              Default Currency
            </label>
          </div>
          <select
            onChange={(e) => setDefaultCurrency(e.target.value)}
            className={`custom-select ${styles.form_control}`}
            id="defaultCurrency"
            value={currencyArray.filter((c) => c === defaultCurrency)}
          >
            <option value=''>Select Default Currency</option>
            {currencyArray && currencyArray.map((c) => <option value={c} selected={(defaultCurrency===c)}>{c}</option>)}
          </select>
        </div>

        <div className={`input-group mb-3 ${styles.input_group}`}>
          <div className="input-group-prepend">
            <label
              className={`input-group-text ${styles.input_group_text}`}
              htmlFor="timeFormat"
            >
              Time Format
            </label>
          </div>
          <select
            onChange={(e) => setTimeFormat(e.target.value)}
            className={`custom-select ${styles.form_control}`}
            id="timeFormat"
            value={timeFormat}
          >
          <option value=''>Select Time Format</option>
            <option value="A" selected={(timeFormat==="A")}>AM / PM</option>
            <option value="H" selected={(timeFormat==="H")}>24 H</option>
          </select>
        </div>

        <div className={`input-group mb-3 ${styles.input_group}`}>
          <div className="input-group-prepend">
            <label
              className={`input-group-text ${styles.input_group_text}`}
              htmlFor="dateFormat"
            >
              Date Format
            </label>
          </div>
          <select
            onChange={(e) => setDateFormat(e.target.value)}
            className={`custom-select ${styles.form_control}`}
            id="dateFormat"
            value={timeFormatArray.filter((t) => t === dateFormat)}
          >
          <option value=''>Select Date Format</option>
            {timeFormatArray && timeFormatArray.map((t) => <option value={t}>{t}</option>)}
          </select>
        </div>
        <Row>
          <Col md={4} xs={6}>
            <div className="input-group-prepend ">
              <label
                  className={`input-group-text ${styles.input_group_text}`}
                  htmlFor="numberFormat"
              >
                Number Format
              </label>
            </div>
          </Col>
          <Col md={8} xs={3}>
            <Row>
              <div className={"col-xs-3 col-md-auto"} >
                <input type="radio" value={"1'000.00"} checked={numberFormat==="1'000.00"} onChange={(e)=>setNumberFormat(e.target.value)} name={"numberFormat"}/>
                <label htmlFor="1">1'000.00</label>
              </div>
              <div className={"col-xs-3 col-md-auto"} >
                <input type="radio" value={"1,000.00"} checked={numberFormat==="1,000.00"} onChange={(e)=>setNumberFormat(e.target.value)} name={"numberFormat"}/>
                <label htmlFor="2">1,000.00</label>
              </div>
              <div className={"col-xs-3 col-md-auto"}>
                <input type="radio" value={"1.000,00"} checked={numberFormat==="1.000,00"} onChange={(e)=>setNumberFormat(e.target.value)} name={"numberFormat"} />
                <label htmlFor="3">1.000,00</label>
              </div>
            </Row>
          </Col>
        </Row>

        <div className="d-flex justify-content-center">
          <button className={styles.settings_save} onClick={handleSave}>
            Save Changes
          </button>
        </div>
      </div>
    </div>
  );
}

export default Settings;
