import { 
    ADD_NEW_FEE,
    DELETE_FEE,
    EDIT_FEE_LIST,
    EDIT_RATES,
    ADD_NEW_TAX,
    DELETE_TAX,
    EDIT_TAX_LIST,
  } from './types';
  
  const INITIAL_STATE = {
    feesList: [{rentalSelection: "Rental 1",
    Name: "Reugular",
    id:0,
    Type: "percentage",
    Modality: "pernight",
    Amount:45
    }],
    rateSettings: [
      {
        GuestNum: "2",
        dailyRate: "55",
        //dateRange: "Feb 2, 2021- Feb 13, 2021",
        discount: true,
        fixRate: false,
        guestLimit: "7",
        minstay: "1",
        monthDiscount: "4",
        monthPrice: "40",
        rentalId: 10,
        weekDiscount: "3.5",
        weekPrice: "10",
      }
    ],
    taxList:[{
      id:1,
      amount: 786,
      rentalSelection: "Rental 1",
      tax_name: "TAX 2Tbt",
      tax_type: "percentage",
    }]
  };
  
  export default function (state = INITIAL_STATE, action) {
    switch (action.type) {
      case ADD_NEW_FEE:
        return {
          ...state,
          feesList: state.feesList.concat(action.payload) 
        };
      case DELETE_FEE:    
        return {    
            ...state,    
            feesList: state.feesList.filter(item => item.id !== action.payload)    
        };
      case EDIT_FEE_LIST:   
        const FeeArrayData = state.feesList;
        const updateIndex = FeeArrayData.findIndex(({id}) => id === action.payload.id);
        if (updateIndex >= 0) {
            FeeArrayData[updateIndex] = action.payload
        } 
        return {    
            ...state,    
            feesList: FeeArrayData   
        }; 
      case EDIT_RATES:   
        const RateArrayData = state.rateSettings;
        const updateIndexRate = RateArrayData.findIndex(({renatlOrGroup}) => renatlOrGroup === action.payload.renatlOrGroup);
        if (updateIndexRate >= 0) {
            RateArrayData[updateIndexRate] = action.payload
        }
        return {    
            ...state,    
            rateSettings: RateArrayData   
        }; 
      case ADD_NEW_TAX:
        return {
          ...state,
          taxList: state.taxList.concat(action.payload) 
        };
      case DELETE_TAX:    
        return {    
            ...state,    
            taxList: state.taxList.filter(item => item.id !== action.payload)    
        };
      case EDIT_TAX_LIST:   
        const TaxArrayData = state.taxList;
        const updateIndexTax = TaxArrayData.findIndex(({id}) => id === action.payload.id);
        if (updateIndexTax >= 0) {
            TaxArrayData[updateIndexTax] = action.payload
        } 
        return {    
            ...state,    
            taxList: TaxArrayData   
        }; 
      default:
        return state;
    }
  }
  