import { 
    ADD_NEW_FEE,
    DELETE_FEE,
    EDIT_FEE_LIST,
    EDIT_RATES,
    ADD_NEW_TAX,
    DELETE_TAX,
    EDIT_TAX_LIST,
  } from './types';

export const addFee = data => ({
    type: ADD_NEW_FEE,
    payload: data
});

export const deleteFee=id=>({
    type: DELETE_FEE,
    payload: id
})

export const editFees=data=>({
    type: EDIT_FEE_LIST,
    payload: data
})

export const editRates=data=>({
    type: EDIT_RATES,
    payload: data
})

export const addTax = data => ({
    type: ADD_NEW_TAX,
    payload: data
});

export const deleteTax=id=>({
    type: DELETE_TAX,
    payload: id
})
export const editTax=data=>({
    type: EDIT_TAX_LIST,
    payload: data
})